#resume {
    margin: 2rem 0;
    .resume-container {}
}

.adress-card-info {
    color: #052422;
    font-size: 110%;
    margin-top: 20px;
    .address-card-info-group {
        display: flex;
        align-items: center;
        margin-bottom: 10px;
        .fa {
            &.icon-location {
                font-size: 34px;
            }
            &.icon-phone {
                font-size: 26px;
            }
            &.icon-mail {
                font-size: 22px;
            }
        }
        .contact-info-text {
            margin: 0
        }
    }
}

.social {
    position: relative;
    height: 3em;
    width: 13.5em;
    margin: 70px auto;
    li {
        display: block;
        height: 4em;
        line-height: 4em;
        margin: -2.2em;
        position: absolute;
        -webkit-transition: -webkit-transform .7s;
        -moz-transition: -moz-transform .7s;
        -ms-transition: -ms-transform .7s;
        -o-transition: -o-transform .7s;
        transition: transform .7s;
        -webkit-transform: rotate(45deg);
        -moz-transform: rotate(45deg);
        -ms-transform: rotate(45deg);
        -o-transform: rotate(45deg);
        transform: rotate(45deg);
        text-align: center;
        width: 4em;
        &.list-xing {
            background: #155b9d;
            left: 50%;
            top: 0%;
            padding-top: 5px;
        }
        &.list-linkedin {
            background: #1a9ec4;
            bottom: 0;
            left: 25%;
        }
        &.list-github {
            background: #3f7aa3;
            bottom: 0;
            left: 75%;
        }
        a {
            color: #fffdf0;
            display: inline-flex;
            align-items: center;
            height: 4em;
            line-height: 6em;
            text-align: center;
            -webkit-transform: rotate(-45deg);
            -moz-transform: rotate(-45deg);
            -ms-transform: rotate(-45deg);
            -o-transform: rotate(-45deg);
            transform: rotate(-45deg);
            width: 4em;
            &:hover {
                -webkit-transform: scale(1.3, 1.3) rotate(45deg);
                -moz-transform: scale(1.3, 1.3) rotate(45deg);
                -ms-transform: scale(1.3, 1.3) rotate(45deg);
                -o-transform: scale(1.3, 1.3) rotate(45deg);
                transform: scale(1.3, 1.3) rotate(45deg);
            }
            i {
                margin: 0 auto
            }
        }
    }
}

.tools-container {
    ul {
        li {
            i {
                &.fa-html5 {
                    font-size: 32px
                }
                &.fa-css3 {
                    font-size: 22px
                }
                &.fa-code {
                    font-size: 22px
                }
            }
        }
    }
}

.resume-mobile {
    display: none
}

// Extra small devices (portrait phones, less than 576px)
@media (max-width: 575.98px) {}

// Small devices (landscape phones, less than 768px)
@media (max-width: 767.98px) {}

// Medium devices (tablets, less than 992px)
@media (max-width: 991px) {
    .resume-desktop {
        display: none
    }
    .resume-mobile {
        display: block
    }
}

// Large devices (desktops, less than 1200px)
@media (max-width: 1199) {}